"use client"

import { useState } from "react"
import { ChevronDown } from "lucide-react"

const faqs = [
  {
    question: "Free trial qanday ishlaydi?",
    answer:
      "Ro‘yxatdan o‘tganingizdan so‘ng sizga avtomatik tarzda 24 soatlik bepul foydalanish huquqi beriladi."
  },
  {
    question: "Testlar qancha vaqt davom etadi?",
    answer:
      "Har bir test uchun alohida vaqt belgilanadi va test boshlangan zahoti taymer ishga tushadi."
  },
  {
    question: "To‘lovni qanday amalga oshiraman?",
    answer:
      "To‘lovlar avtomatik emas. Telegram orqali admin bilan bog‘lanib, to‘lov qilasiz."
  },
  {
    question: "Obuna muddati tugasa nima bo‘ladi?",
    answer:
      "Agar obuna muddati tugasa, testlarga kirish yopiladi va obunani yangilash talab qilinadi."
  }
]

export default function FAQSection() {
  const [openIndex, setOpenIndex] = useState<number | null>(null)

  return (
    <section className="mt-24">
      <div className="mx-auto max-w-3xl">
        <h2 className="mb-8 text-center text-3xl font-bold">
          Frequently Asked Questions
        </h2>

        <div className="space-y-4">
          {faqs.map((faq, index) => {
            const isOpen = openIndex === index

            return (
              <div
                key={index}
                className="rounded-lg border bg-card"
              >
                <button
                  onClick={() =>
                    setOpenIndex(isOpen ? null : index)
                  }
                  className="flex w-full items-center justify-between px-6 py-4 text-left font-medium"
                >
                  <span>{faq.question}</span>
                  <ChevronDown
                    className={`h-5 w-5 transition-transform ${
                      isOpen ? "rotate-180" : ""
                    }`}
                  />
                </button>

                {isOpen && (
                  <div className="px-6 pb-4 text-sm text-muted-foreground">
                    {faq.answer}
                  </div>
                )}
              </div>
            )
          })}
        </div>
      </div>
    </section>
  )
}
